import React from 'react';
import { Helmet } from 'react-helmet';

export default function Fonts() {
  return (
    <Helmet>
      <link
        href="//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css"
        rel="stylesheet"
        type="text/css"
      />
    </Helmet>
  );
}
